@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Oswald:wght@500&family=Ubuntu:wght@300&display=swap");
:root {
  --primary-app-color: #a9e73b;
  --secondary-app-color: #6ca703;
  --standout-color: #f0ff6b;
}
.App {
}

a {
  text-decoration: none;
}
* {
  padding: 0px;
  margin: 0px;
}

.herosection {
  background: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
  clip-path: polygon(0 0, 100% 0, 100% 90%, 30% 100%, 0 86%);
}

.nav-items {
  cursor: pointer;
  color: #ffffff;
  font-size: 1.3rem;
  font-family: "Ubuntu";
}

.brand-name {
  color: #ffffff;
  font-size: 2rem;
  font-family: "Oswald";
}

.px-10 {
  padding-inline: 90px;
}

.herosection-2 {
  padding-top: 60px;
}

.text-section {
  padding-top: 52px;
}

.text {
  font-family: "Ubuntu";
  color: #ffffff;
  font-weight: 700;
  font-size: 2rem;
}

.myname {
  color: var(--standout-color);
  font-size: 28px;
}

.text2 {
  font-family: "Ubuntu";
  color: #ffffff;
  font-size: 2rem;
}

.pet-name {
  color: var(--standout-color);
}
.subheading {
  font-family: "Ubuntu";
  color: #ffffff;
  font-size: 2rem;
}

.hire-me {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #ffffff;
  background: transparent;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.5s ease-in-out;
  animation: Animation 2s ease-in-out infinite;
}

.resume {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #ffffff;
  background: #f75420;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.5s ease-in-out;
}

.hire-me:hover {
  transform: translateY(-5px);
  background-color: var(--standout-color);
  color: #1c1c1c;
}

.resume:hover {
  transform: translateY(-5px);
  background-color: var(--standout-color);
  color: #1c1c1c;
}

@keyframes Animation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.image {
  width: 300px;
  border-radius: 50%;
  object-fit: content;
  display: flex;
  justify-content: center;
  padding-bottom: 12px;
  border: 1px solid white;
}

/* about me */

.abt-me-text {
  font-family: "Ubuntu";
  font-weight: 800;
  font-size: 3.5rem;
  letter-spacing: 2px;
  text-align: center;
}

.abt-me-subtext {
  font-family: "Ubuntu";
  font-weight: 700;
  font-size: 1.5rem;
  text-align: center;
  opacity: 75%;
}

.aboutme-inside {
  width: 70%;
  margin-inline: auto;
}

.some-text-abt-me {
  font-family: "Ubuntu";
  font-weight: 600;
  font-size: 1.2rem;
  opacity: 70%;
}

.some-text-abt-me-2 {
  font-family: "Ubuntu";
  font-weight: 600;
  font-size: 1.2rem;
  opacity: 70%;
}

.skills {
  font-family: "Ubuntu";
  font-weight: 600;
  font-size: 1rem;
  opacity: 75%;
  margin-top: 10px;
}

.options {
  width: 30%;
  transition: all 0.3s ease-in-out;
  padding-block: 24px;
  padding-inline: 24px;
  color: #000000;
  cursor: pointer;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.options:hover {
  width: 100%;
  background: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
  color: #ffffff;
}

.icon-span {
  margin-block: 24px;
  margin-inline: 24px;
}

.selected {
  background: linear-gradient(
    -45deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
  width: 100%;
  color: #ffffff;
}

.bg-new {
  background: linear-gradient(
    80deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
}

.bg-new2 {
  background: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
}
.resume-outer-section {
  padding-top: 100px;
  padding-bottom: 100px;
  background: var(--primary-app-color);
  color: #ffffff;
  clip-path: polygon(0 11%, 100% 0, 100% 100%, 0 100%);
}

.resume-new-section {
  width: 70%;
  height: 400px;
  margin-top: 50px;
  align-self: center;
  background: #ffffff;
  box-shadow: 10px 10px 31px 2px #1c1c1c;
}

.contact-me {
  color: #ffffff;
}

.relative-bg {
  clip-path: polygon(0 5%, 100% 14%, 100% 80%, 0% 96%);
  background: linear-gradient(
    -90deg,
    var(--primary-app-color),
    var(--secondary-app-color)
  );
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -4;
}

.contact-inner-section {
  padding-block: 118px;
}

.px-9 {
  padding-inline: 90px;
}

.flex-rows {
  flex-direction: row;
  justify-content: space-between;
}

.navbar-option-mobile {
  display: none;
}

.lottie1 {
  width: 500px;
  height: 500px;
}
/* mobile view */
@media all and (max-width: 786px) {
  .navbar-option-mobile {
    display: block;
    width: 100vw;
    height: 45px;
    position: fixed;
    z-index: 44;
  }
  .flex-rows {
    flex-direction: column;
    justify-content: start;
  }

  .navbar-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
    transform: translateX(-700px);
  }
  .transition-all {
    transition: all 0.3s ease-in-out;
  }
  .navbar-fixed-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
  }
  .px-9 {
    padding-inline: 0px;
  }
  .px-10 {
    padding-inline: 10px;
  }
  .nav-items {
    padding-block: 20px;
  }

  .brand-name {
    font-size: 1.4rem;
    padding-inline: 22px;
  }

  .bars {
    cursor: pointer;
    height: 4px;
    width: 40px;
    background: #ffffff;
    float: right;
    z-index: 33;
    transform: translateY(20px);
  }
  .bars::before {
    display: block;
    position: relative;
    top: 9px;
    content: "";
    color: #ffffff;
    width: 40px;
    height: 4px;
    background: #ffffff;
  }
  .bars::after {
    display: block;
    position: relative;
    bottom: 12px;
    content: "";
    color: #ffffff;
    width: 40px;
    height: 4px;
    background: #ffffff;
  }

  .text {
    font-size: 1.2rem;
    text-align: center;
  }

  .myname {
    font-size: 1.2rem;
    text-align: center;
  }
  .text2 {
    font-size: 1.2rem;
    text-align: center;
  }

  .subheading {
    font-size: 1.2rem;
    text-align: center;
  }
  .hire-me {
    font-size: 1rem;
    padding: 10px 10px;
    margin-left: 70px;
    margin-block: 20px;
  }
  .resume {
    font-size: 1rem;
    padding: 10px 10px;
    margin-block: 20px;
  }

  .image {
    width: 200px;
    object-fit: content;
    display: flex;
    justify-content: center;
    padding-bottom: 17px;
    border: 1px solid white;
  }

  .abt-me-text {
    font-size: 1.3rem;
  }
  .abt-me-subtext {
    font-size: 1rem;
  }
  .aboutme-inside {
    width: 91%;
  }
  .resume-new-section {
    height: 30%;
    width: 100%;
  }
}
