}

.hire-me {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #ffffff;
  background: transparent;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.5s ease-in-out;
  -webkit-animation: Animation 2s ease-in-out infinite;
          animation: Animation 2s ease-in-out infinite;
}

.resume {
  font-size: 1.3rem;
  font-family: "Ubuntu";
  color: #ffffff;
  background: #f75420;
  border-radius: 25px;
  margin-inline: 12px;
  padding: 5px 40px;
  border: 2px solid white;
  transition: all 0.5s ease-in-out;
}

.hire-me:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  background-color: #f0ff6b;
  background-color: var(--standout-color);
  color: #1c1c1c;
}

.resume:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  background-color: #f0ff6b;
  background-color: var(--standout-color);
  color: #1c1c1c;
}

@-webkit-keyframes Animation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes Animation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.image {
  width: 300px;
  border-radius: 50%;
  object-fit: content;
  display: flex;
  justify-content: center;
