/* mobile view */
@media all and (max-width: 786px) {
  .navbar-option-mobile {
    display: block;
    width: 100vw;
    height: 45px;
    position: fixed;
    z-index: 44;
  }
  .flex-rows {
    flex-direction: column;
    justify-content: start;
  }

  .navbar-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
    -webkit-transform: translateX(-700px);
            transform: translateX(-700px);
  }
  .transition-all {
    transition: all 0.3s ease-in-out;
  }
  .navbar-fixed-new {
    position: fixed;
    height: 100vh;
    width: 60vw;
    z-index: 33;
  }
  .px-9 {
    padding-inline: 0px;
  }
  .px-10 {
    padding-inline: 10px;
  }
  .nav-items {
    padding-block: 20px;
  }

  .brand-name {
    font-size: 1.4rem;
    padding-inline: 22px;
  }

  .bars {
    cursor: pointer;
    height: 4px;
    width: 40px;
    background: #ffffff;
    float: right;
    z-index: 33;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  .bars::before {
    display: block;
    position: relative;
    top: 9px;
    content: "";
    color: #ffffff;
    width: 40px;
    height: 4px;
    background: #ffffff;
  }
  .bars::after {
    display: block;
    position: relative;
    bottom: 12px;
    content: "";
    color: #ffffff;
    width: 40px;
    height: 4px;
    background: #ffffff;
  }

  .text {
    font-size: 1.2rem;
    text-align: center;
  }

  .myname {
    font-size: 1.2rem;
    text-align: center;
  }
  .text2 {
    font-size: 1.2rem;
    text-align: center;
  }

  .subheading {
    font-size: 1.2rem;
    text-align: center;
  }
  .hire-me {
    font-size: 1rem;
    padding: 10px 10px;
    margin-left: 70px;
    margin-block: 20px;
  }
  .resume {
    font-size: 1rem;
    padding: 10px 10px;
    margin-block: 20px;
  }

  .image {
    width: 200px;
    object-fit: content;
    display: flex;
    justify-content: center;
    padding-bottom: 17px;
    border: 1px solid white;
  }

  .abt-me-text {
    font-size: 1.3rem;
  }
  .abt-me-subtext {
    font-size: 1rem;
  }
  .aboutme-inside {
    width: 91%;
  }
  .resume-new-section {
    height: 30%;
    width: 100%;
  }
}

