.contact-me-card {
  background: #1c1c1c;
  width: 70%;
  box-shadow: 10px 10px 63px 4px rgb(243 240 240);
  height: 550px;
  margin-inline: auto;
}

.get-in-touch {
  color: #ffffff;
  font-family: "Ubuntu";
  font-weight: 700;
  font-size: 2rem;
}

.input-groups {
  border: none;
  border-radius: 10px;
  padding: 10px 10px;
}

.btn {
  background-color: #f0ff6b;
}

.card-contact-right {
  background: #254d02;
  box-shadow: 10px 3px 33px 4px rgb(243 240 240);
  border-radius: 20px;
  width: 60%;
  padding: 30px;
}

.lottie2 {
  height: 450px;
  width: 450px;
}

@media all and (max-width: 786px) {
  .contact-me-card {
    height: 80%;
    width: 100%;
  }
  .card-contact-right {
    width: 94%;
  }
  .get-in-touch {
    font-size: 2rem;
  }
}
