.university-name {
  font-family: "Ubuntu";
  color: #f58a13;
  font-size: 1.4rem;
  font-weight: 800;
}

.university-degree {
  font-family: "Ubuntu";
  font-size: 0.8rem;
  font-weight: 800;
  color: #c37a04;
  text-transform: uppercase;
}

.year-passout {
  font-family: "Ubuntu";
  font-size: 1rem;
  background: #f0ff6b;
  color: #771801;
  padding: 10px 10px;
  border-radius: 20px;
}

@media all and (max-width: 786px) {
  .university-name {
    font-size: 1rem;
  }

  .university-degree {
    font-size: 0.7rem;
  }

  .year-passout {
    font-size: 10px;
    padding: 5px 5px;
    border-radius: 20px;
  }
}
